import uniIcon from "@/components/uni/uni-icon/uni-icon.vue"
import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	data() {
		return {
			content: [],
			list:[],
			address: '',
			time: '09:00',
			time_close: '19:00'
		};
	},
	onLoad() {
		this.content=this.$api.json_cms.content
	},
	components: {
		uniIcon,
		wPicker
	},
	methods: {
		TimeChange(e) {
			this.time = e.detail.value
		},
		bindTimeChange: function(e) {
			this.time = e.target.value
		},
		bindTimeChange_close: function(e) {
			this.time_close = e.target.value
		},



		switch1Change: function(e) {
			if (e.target.value) {
				this.list.shop_state = true //   1   checked的值为布尔值
			} else {
				this.list.shop_state = false  //   0
			}
			console.log(this.list.shop_state)
		},
		//选择位置
		biaozu() {
			const that = this
			// uni.showLoading({
			// 	title: '加载中',
			// 	mask: true
			// });


			uni.getSetting({
				success(res) {
					if (!res.authSetting['scope.userLocation']) {
						uni.authorize({
							scope: 'scope.userLocation',
							success() {
								that.chooseLocation()
								return;
							}
						})
					} else {
						that.chooseLocation()
						return;
					}
				}
			})





		},
		chooseLocation() {
			const that = this
			uni.chooseLocation({
				success: function(res) {
					console.log('address:', res)
					that.address = res.name
					that.list.position = res
				}
			});
		},
	}
}
